import {Canvas} from '@react-three/fiber';




export default function RJS(){
    return(
        <>
        <Canvas>
      <mesh>
        <boxGeometry args={[1, 2, 3]} />
        <meshPhongMaterial />
      </mesh>
      <ambientLight intensity={0.1} />
      <directionalLight position={[5, 0, 10]} color="red" />
    </Canvas>
        </>
    )
}